









import { Component, Vue } from 'vue-property-decorator';
import Banner from '../../components/Banner.vue';

@Component({
  components: {
    Banner,
  },
})

export default class ExamInformation extends Vue {
  link() {
    if (this.isSelection) this.$router.push('/main');
    else this.$router.push('/main/information/selection');
  }

  get isSelection() {
    return this.$route.path.indexOf('selection') >= 0;
  }

  get selectionTitle() {
    return this.$route.query.title;
  }
}
